.hamburger-menu{height:100%;width:100%;display:flex;justify-content:center;align-items:center}.hamburger-icon,.cancel-icon{width:50px;height:50px;cursor:pointer}.hamburger-icon{color:#e8a506}.cancel-icon{color:#b34d09;margin:5px;float:right}.cancel-icon-wrapper{align-self:end}.menu{position:fixed;top:0;left:0;z-index:1;width:100%;background-color:#4f2c2c;background-image:linear-gradient(to left, #4f2c2c, #6b3c3c);display:flex;flex-direction:column;align-items:center}.menu.hide{display:none}.menu-items{width:100%;height:200px;overflow:hidden;position:relative;border-bottom:1px solid #ffb300}.menu-items-wrapper{height:100%;height:100%;position:absolute;top:0;bottom:0;left:0;right:-17px;overflow-y:scroll}.menu-item{width:100%;height:50px;padding:8px;text-align:center;border-bottom:1px solid #472828}.menu-item:first-of-type{border-top:1px solid #472828}.menu-item h5{font-size:1.2em}.menu-item:hover{background-color:#6b3c3c}