.header{height:100%;background-color:#4f2c2c;background-image:linear-gradient(to left, #4f2c2c, #6b3c3c);border-bottom:1px solid #ffb300;color:#fff;display:flex;align-items:center}.header a{color:#e8a506}a:hover{color:#b34d09;background-color:#6b3c3c}.logo{margin:5px;width:67px;height:70px}.wrapper{width:100%;height:100%;margin:0px;display:flex;justify-content:space-between;align-items:center;flex-wrap:nowrap}.link-section{height:100%;display:flex;align-items:end}.link-section div:first-of-type{height:50%}.link-section a:last-of-type{border-right:2px solid #472828}.link{font-size:1.3em;min-width:120px;padding:10px;text-align:center;white-space:nowrap;border-left:2px solid #472828;display:flex;justify-content:center}.menu-section{display:none}@media(max-width: 760px){.logo-section,.link-section{display:none !important}.menu-section{width:100%;height:100%;display:flex;align-items:center;justify-content:center}}